
import React, {useMemo} from 'react'
import{useTable} from 'react-table'
import { COLUMNS } from './columns'
import './table.css'
import MOCK_DATA from './MOCK_DATA.json'
import { Button} from '../ButtonsElements';
//import { IndicatorContainer,IndicatorBtnWrapper ,ArrowForward,ArrowRight,StyledForm,StyledFormWrapper,StyledInput} from '../IndicatorElements'
import{ getTableProps,getTableBodyProps, headerGroups, rows, prepareRow, BasicTable } from './BasicTable';



const DatatableSection = () => {
    const columns = useMemo(() => COLUMNS, [])
    const data = useMemo(()=> MOCK_DATA, [])

    //const [Period,setPeriod]=useState('3m');

    const tableInstance = useTable({
        columns,
        data
    })

    const {
        getTableProps,
        getTableBodyProps, 
        headerGroups, 
        rows, 
        prepareRow
    } = tableInstance
    

return (

    <>
   
    
    <table {...getTableProps()}>

        
        <thead>
            {
                headerGroups.map(headerGroups => (
                <tr {...headerGroups.getHeaderGroupProps()}>
                    {
                        headerGroups.headers.map( column =>(
                            <th{...column.getHeaderProps()}>{column.render('Header')}</th>
                        ) )
                    }
                  
                </tr>

            ))}
                
        </thead>

        <tbody {...getTableBodyProps()}>
        {rows.map((row) => {
            prepareRow(row)
            return (
                <tr{...row.getRowProps()}>
                    {row.cells.map((cell) => {
                        return <td {...cell.getCellProps()}>{cell.render('Cell')}</td>
                        }

                        )
                    }
                     
                    </tr>
                )
            })
        }
       
      
        </tbody>
    
        
    </table>
    </>

  );

};

export default BasicTable