import React, {useMemo, useState} from 'react'
import{useTable} from 'react-table'
import { COLUMNS } from './columns'
import './table.css'
import MOCK_DATA from './MOCK_DATA.json'


export const BasicTable = () => {
    const columns = useMemo(() => COLUMNS, [])
    const data = useMemo(()=> MOCK_DATA, [])

    const tableInstance = useTable({
        columns,
        data
    })
    const [Period,setPeriod]=useState('3m');
    const handlePeriod = () => {
        console.log(Period);}

    const {
        getTableProps,
        getTableBodyProps, 
        headerGroups, 
        rows, 
        prepareRow
    } = tableInstance
  return (
      <>
      <select
                       value={Period}
                       onChange={(e)=>setPeriod(e.target.value)}
                        >
                    {/* <option value="1d">1day</option>
                    <option value="5d">5day</option> */}
                    <option value="1mo">1month</option>
                    <option value="3mo">3month</option>
                    <option value="6mo">6month</option>
                    <option value="1y">1year</option>
                    <option value="5y">5year</option>
                    </select>
    <table {...getTableProps()}>
        <thead>
            {
                headerGroups.map(headerGroups => (
                <tr {...headerGroups.getHeaderGroupProps()}>
                    {
                        headerGroups.headers.map( column =>(
                            <th{...column.getHeaderProps()}>{column.render('Header')}</th>
                        ) )
                    }
                  
                </tr>

                ))
            }
        
        </thead>
        <tbody {...getTableBodyProps()}>
        {
            rows.map(row => {
                prepareRow(row)
                return (
                    <tr{...row.getRowProps()}>
                    {
                        row.cells.map((cell) => {
                            return <td{...cell.getCellProps()}>{cell.render('Cell')}</td>
                        }

                        )
                    }
                     
                    </tr>
                )
            })
        }
        
        </tbody>
        
        
    </table>
    </>
  )
}
